import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import ImageBackground from "../images/images_homenowords-07.jpg"
import { makeStyles, TextareaAutosize, TextField } from "@material-ui/core"
import FlickerEffect from "../components/flickerEffect"
import { useForm } from 'react-hook-form';
const useStyles = makeStyles(() => ({
   image: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      '& div': {
         backgroundImage: `url(${ImageBackground})`,
         backgroundSize: '100% auto',
         backgroundRepeat: 'no-repeat',
         backgroundPosition: 'center',
         display: 'flex',
         justifyContent: 'center',
         alignItems: 'center',
         position:'relative',
         "@media (max-width: 767px)": {
            height: '25vh',
            width: '90vw',
         },
         "@media (min-width: 768px) and (max-width: 1023px)": {
            height: '33vh',
            width: '90vw',
         },
         "@media (min-width: 1024px) and (max-width: 1280px)": {
            height: '35vw',
            width: '70vw',
         },
         "@media (min-width: 1281px) and (max-width: 1439px)": {
            height: '50vh',
            width: '50vw',
         },
         "@media (min-width: 1440px)": {
            height: '48vh',
            width: '45vw'
         },
      }
   },
   dynamicContent: {
      position: 'absolute',
      borderTop: '1px solid white',
      borderBottom: '1px solid white',
      textTransform: 'uppercase',
      color: "white",
      padding: '5px 0',
      textAlign: 'center',
      top: '42%',
      "@media (max-width: 767px)": {
         width: '50vw',
      },
      "@media (min-width: 768px) and (max-width: 1023px)": {
         width: '40vw',
      },
      "@media (min-width: 1024px) and (max-width: 1280px)": {
         width: '40vw',
      },
      "@media (min-width: 1281px) and (max-width: 1439px)": {
         width: '30vw',
      },
      "@media (min-width: 1440px)": {
         width: '23vw',
      },
   },
   contactView: {
      display: 'flex',
      justifyContent: 'space-around',
      width: '55vw',
      marginTop: 25,
      "@media (max-width: 767px)": {
         flexDirection: 'column-reverse'
      },
      "@media (min-width: 768px) and (max-width: 1023px)": {
         width: '85vw',
         top: '33%',
      },
      "@media (min-width: 1024px) and (max-width: 1280px)": {
         width: '80vw',
         top: '50%',
      },
      '& div:nth-child(2)': {
         width: '35vw',
         "@media (max-width: 767px)": {
            width: '90vw'
         },
         "@media (min-width: 768px) and (max-width: 1023px)": {
            width: '55vw',
            top: '33%',
         },
         "@media (min-width: 1024px) and (max-width: 1280px)": {
            width: '55vw',
         },
      },
      
   },
   label: {
      margin: 0,
      fontWeight: 'bold',
      fontSize: '0.8rem',
      display:'block'
   },
   subLabel: {
      margin: 0,
      fontSize: '0.8rem'
   },
   field: {
      margin: '20px 0',
      width: '100%'
   },
   nameField: {
      display: 'flex',
      '& div:nth-child(1)': {
         marginRight: '2%',
         width: '100%'
      },
      '& div:nth-child(2)': {
         width: '100%'
      }
   },
   width100percent: {
      width: '100%',
      '& .MuiOutlinedInput-input': {
         padding: 6,
      },
   },
   textArea:{
         padding: 6,
         border:'1px solid rgba(0, 0, 0, 0.23);',
         width: '100%',
         borderRadius:4,
         '&:focus':{
            border: '2px solid #005b99',
            outline:0,
         }
   },
   contactUs: {
      
      '& p:nth-child(1)': {
         fontSize: '1.2rem',
         fontWeight: 'bold',
         marginBottom: 10
      },
      '& p:nth-child(2)': {
         margin: 0
      },
      '& p:nth-child(3)': {
         margin: 0
      }
   },
   wrapAll: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      "@media (max-width: 767px)": {
         alignItems: 'flex-start',
      },
   },
   button: {
      padding: '6px 10px',
      border: '1px solid #b21c8a',
      borderRadius: 4,
      backgroundColor: '#b21c8a',
      color: 'white',
      fontWeight: 'bold',
      width: 100
   },
   errorsMes: {
      color: 'red',
      fontSize: '0.8rem',
      fontStyle: 'italic'
   },
   buttonSubmit:{
      textAlign: 'center', 
      marginBottom: 25
   }
}));

const listWordContact = ['NEED HELP', 'NEED ADVICE', 'HAVE QUESTIONS'];

const Contact = ({ data, location }) => {
   const classes = useStyles()
   const siteTitle = data.site.siteMetadata?.title || `Title`
   const { register, handleSubmit, errors, reset } = useForm({
      mode: 'onSubmit',
      reValidateMode: 'onChange',
   });
   const onSubmit = (data) => {
      console.log(data);
      reset();
   }
   return (
      <Layout location={location} title={siteTitle}>
         <Seo title="Contact" description="Contact to us"/>
         <div className={classes.wrapAll}>
            <div className={classes.image}>
               <div>
                  <p className={classes.dynamicContent}>
                     <FlickerEffect listWord={listWordContact}></FlickerEffect>
                  </p>
               </div>
            </div>
            <div className={classes.contactView}>
               <div className={classes.contactUs}>
                  <p>Contact Us</p>
                  <p>info@izundo.com</p>
                  <p>(415) 699-0381</p>
               </div>
               <div>
                  <form onSubmit={handleSubmit(onSubmit)}>
                     <div>
                        <p className={classes.label}>Name</p>
                        <div className={classes.nameField}>
                           <div>
                              <TextField 
                              variant="outlined" 
                              className={classes.width100percent} 
                              name="firstName" 
                              inputRef={register({ required: true })} 
                              color={errors.firstName?"secondary":"primary"}
                              />
                              <p className={classes.subLabel}>First Name</p>
                              {errors.firstName && <span className={classes.errorsMes}>*First name is required</span>}
                           </div>
                           <div>
                              <TextField 
                              variant="outlined" 
                              className={classes.width100percent} 
                              name="lastName" 
                              inputRef={register({ required: true })} 
                              color={errors.lastName?"secondary":"primary"}
                              />
                              <p className={classes.subLabel}>Last Name</p>
                              {errors.lastName && <span className={classes.errorsMes}>*Last name is required</span>}
                           </div>
                        </div>
                     </div>
                     <div className={classes.field}>
                        <p className={classes.label}>Company</p>
                        <TextField variant="outlined" className={classes.width100percent} name="company" inputRef={register}/>
                     </div>
                     <div className={classes.field}>
                        <labpel className={classes.label}>Email Address</labpel>
                        <TextField
                        variant="outlined" 
                        className={classes.width100percent} 
                        name="email" 
                        color={errors.email?"secondary":"primary"}
                        inputRef={register({
                           required: true,
                           pattern: /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/,
                        })} />
                        {errors.email && <span className={classes.errorsMes}>*Please format email correctly</span>}
                     </div>
                     <div className={classes.field}>
                        <p className={classes.label}>Message</p>
                        <TextareaAutosize aria-label="empty textarea" rowsMin={5} className={classes.textArea} name="message" ref={register} />
                     </div>
                     <div className={classes.buttonSubmit}>
                        <input type='submit' className={classes.button}></input>
                     </div>
                  </form>
               </div>
            </div>
         </div>
      </Layout>
   )
}

export default Contact

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
